import { TaskInterface } from "@/apps/interfaces/TaskInterface";
import { FolderInterface } from "@/apps/interfaces/AppInterface"
import HisDate from "@/utils/Date"
import UserProperty from "@/composables/userproperty";
import router from "@/router";

enum WorkflowIndex {
    PREGNANCY_STATUS= 1,
    CIRCUMCISION=2,
    TESTING=3,
    REGISTRATION=4,
    RECENCY=5,
    DBS_ORDER=6,
    PARTNER_RECEPTION=7,
    APPOINTMENT=8,
    HTS_CONTACT=9,
    ITEMS_GIVEN=10,
    ART_INITIATION=11,
    REFERRAL=12
}

export const PRIMARY_ACTIVITIES: TaskInterface[] = [
    {
        id: 'pregnancy status',
        name: 'Pregnancy status',
        icon: 'current_preg.png',
        workflowID: 'PREGNANCY STATUS',
        patientUrl: `/hts/status/pregnancy`,
        offlineWorkflowIndex: WorkflowIndex.PREGNANCY_STATUS,
        offlineWorkflowCondition({ person }) {
            return person?.gender === 'F' && HisDate.getAgeInYears(person.birthdate) > 10
        },
        condition: ({ gender }: any) => gender === 'F'
    },
    {
        id: 'circumcision status',
        name: 'Circumcision status',
        icon: 'medical_male_circumcision.png',
        encounterTypeName: 'Circumcision',
        workflowID: 'CIRCUMCISION',
        patientUrl: `/hts/status/circumcision`,
        offlineWorkflowIndex: WorkflowIndex.CIRCUMCISION,
        condition: ({ gender }: any) => gender === 'M',
        offlineWorkflowCondition: ({person}) => person?.gender === 'M'
    },
    {
        id: 'testing',
        name: 'Testing',
        icon: 'lab.png',
        encounterTypeName: 'Testing',
        workflowID: 'Testing',
        offlineWorkflowIndex: WorkflowIndex.TESTING,
        taskCompletionChecklist: [
            'HTS Access Type',
            'Previous HIV Test Results'
        ],
        patientUrl: `/hts/testing`
    },
    {
        id: 'hts_registration',
        name: 'Client Registration',
        icon: 'print.png',
        encounterTypeName: 'REGISTRATION',
        workflowID: 'REGISTRATION',
        offlineWorkflowIndex: WorkflowIndex.REGISTRATION,
        patientUrl: `/hts/complete_professional_registration`,
        offlineWorkflowCondition({ person, encounters }: any) {
            return encounters['Testing'] && (
                encounters['Testing']['Test 2'] && /unknown/i.test(
                    `${person.given_name} ${person.family_name}`
                ))
        }
    },
    {
        id: 'hts referral appointment',
        name: 'Appointment',
        icon: 'summary.png',
        workflowID: 'APPOINTMENT',
        patientUrl: `/hts/appointment`,
        offlineWorkflowIndex: WorkflowIndex.APPOINTMENT,
        offlineWorkflowCondition({ encounters }) {
            const test = encounters['Testing']
            if (!test) return true
            return (!/inconclusive/i.test(`${test['Previous HIV Test Results']}`) &&
                /inconclusive/i.test(`${test['HIV status']}`)) ||
                !/positive/i.test(`${test['HIV status']}`)
        }
    },
    {
        id: 'hts art initiation',
        name: 'ART (Re-)Initiation',
        icon: 'referral.png',
        workflowID: 'ART Enrollment',
        encounterTypeName: "ART Enrollment",
        patientUrl: `/hts/art/initiation`,
        offlineWorkflowIndex: WorkflowIndex.ART_INITIATION,
        offlineWorkflowCondition: ({ encounters }: any) => {
            const timeSinceLastMedicationInDays = () => {
                const [strValue, unit] = `${test['Time since last taken medication']}`.split(' ')
                const timeValue = parseInt(strValue)
                switch(unit) {
                    case 'Weeks':
                        return timeValue * 7
                    case 'Months':
                        return timeValue * 30
                    case 'Years':
                        return timeValue * 365
                    default:
                        return timeValue
                    }
            }
            const test = encounters['Testing']
            if (test) {
                return [
                    /positive/i.test(`${test['HIV status']}`),
                    !/positive/i.test(`${test['Previous HIV Test Results']}`),
                    test['Most recent drug taken'] === 'ART' 
                        ? timeSinceLastMedicationInDays() > 7
                        : true
                ].every(Boolean)
            }
            return false
        }
    },
    {
        id: 'hts referral',
        name: 'Referral',
        icon: 'referral.png',
        workflowID: 'REFERRAL',
        patientUrl: `/hts/referral`,
        offlineWorkflowIndex: WorkflowIndex.REFERRAL,
    },
    {
        id: 'hts contact',
        name: 'Contact tracing',
        icon: 'patient-type.png',
        encounterTypeName: "HTS contact",
        workflowID: 'HTS Contact',
        patientUrl: `/hts/contact`,
        offlineWorkflowIndex: WorkflowIndex.HTS_CONTACT,
        offlineWorkflowCondition: ({ encounters }: any) => encounters['Testing'] && encounters['Testing']['HIV status'] === 'Positive'
    },
    {
        id: 'hts partner reception',
        name: 'Partner reception',
        icon: 'edit-user.png',
        patientUrl: `/hts/partner`,
        workflowID: 'Partner Reception',
        offlineWorkflowIndex: WorkflowIndex.PARTNER_RECEPTION,
        offlineWorkflowCondition: ({ person, encounters }: any) => {
            return HisDate.getAgeInYears(person.birthdate) > 10 && encounters['Testing'] && encounters['Testing']['HTS Access Type'] != 'Community'
        }
    },
    {
        id: 'hts art referral outcome',
        name: 'ART Referral Outcome',
        icon: 'aids.png',
        patientUrl: `/hts/referral/outcome`,
    },
    {
        id: 'hts items given',
        name: 'Items Given',
        icon: 'first-aid-kit.png',
        patientUrl: '/hts/items',
        workflowID: 'ITEMS GIVEN',
        offlineWorkflowIndex: WorkflowIndex.ITEMS_GIVEN,
    },
    {
        id: 'hts recency test',
        name: 'Recency',
        icon: 'usage.png',
        patientUrl: '/hts/recency',
        workflowID: 'RECENCY',
        offlineWorkflowIndex: WorkflowIndex.RECENCY,
        async offlineWorkflowCondition({ person, encounters }: any) {
            const prop = UserProperty()
            const userActivities = (await prop.getUserProp('HTS_PROGRAMS'))||''
            return /recency/i.test(userActivities) && HisDate.getAgeInYears(person?.birthdate) > 10 &&
                encounters['Testing'] && encounters['Testing']['HIV status'] === 'Positive'
        }
    },
    {
        id: 'hts dbs result',
        name: 'DBS Result',
        icon: 'lab.png',
        patientUrl: '/hts/dbs'
    },
    {
        id: 'dbs order',
        name: 'DBS Order',
        icon: 'lab.png',
        patientUrl: '/hts/dbs/order',
        offlineWorkflowIndex: WorkflowIndex.DBS_ORDER,
        workflowID: 'DBS ORDER',
        offlineWorkflowCondition: ({ encounters, person }: any) => {
            const test = encounters['Testing']
            const age = HisDate.getAgeInYears(person?.birthdate)
            if (test && test['HIV status'] === 'Positive') {
                return (encounters['RECENCY'] && encounters['RECENCY']['Recency Test'] === 'Recent') ||
                    age < 1 && !/inconclusive/i.test(test['Previous HIV Test Results']) &&
                    !/inconclusive/i.test(test['HIV status'])
            }
            return false
        }
    }
]

