export enum SyncVariable {
    OfflineDB = 'offline_db'
}

export interface LabOrderOffline {
    conceptName: string;
    specimenConceptName: string;
    accessionNumber: string;
    reason: string;
    created: boolean;
}

export interface ObservationOffline {
    id: string|number;
    date?: string;
    userName?: string;
    encounterName: string;
    encounterID?: number;
    patientID?: number;
    encounterTypeID: number;
    observations: Array<any>;
    labOrders: LabOrderOffline[];
    meta?: Record<string, any>;
    uploadStatus?: "InProgress" | "Success" | "NetworkError" | "Pending" | "Cancel" | 'Exception';
    created?: boolean;
}

export interface SyncData {
    id: number|string;
    label: string;
    programID: number;
    programName: string;
    status?: "Error" | "Success" | "Pending" | "Synchronizing";
    statusMessage?: string;
    date?: string;
    userName?: string;
    userID: number;
    personID?: number;
    generatedIDs?: string[];
    personParams?: {
        npid?: string;
        person: Record<string, any>;
        attributes: Array<any>;
        created?: boolean;
        status?: 'Synchronizing' | 'Complete';
    },
    visitMetaByDate?: Record<string, any>;
    obsToSave?: ObservationOffline[],
    complete?: boolean;
}