import { Option } from '@/components/Forms/FieldInterface'
import { Offline } from "@/composables/offline";
import { computed, reactive, ref, unref } from "vue";
import { getPlatformName } from '@/composables/usePlatform'
import { AppEncounterService } from '@/services/app_encounter_service'
import { useRoute } from 'vue-router';
import { toastWarning } from '@/utils/Alerts';
import { ProgramService } from '@/services/program_service';
import { isEmpty, isPlainObject } from 'lodash';
import router from "@/router";
import dayjs from 'dayjs';

const visitID = ref('')

interface HtsVisitParams {
    onInitVisit?: (htsClient: Record<string, any>) => void;
    visitID?: number;
}

export function StartHtsEncounter(params={} as HtsVisitParams) {
    const route = useRoute()
    const providerID = ref(-1)
    const startTime = ref<any>(null)
    const htsClient = reactive({
        age: -1,
        givenName: '',
        familyName: '',
        gender: '',
        birthDate: '',
        village: '',
        district: '',
        currentTA: '',
        npid: '',
        birthEstimated: false
    })

    const visitData = computed(() => Offline().findByID(visitID.value))

    const hasEncounter = (encounterName: string) => !isEmpty(getEncounter(encounterName))

    const getEncounter = (encounterName: string) => {
        try {
            return visitData.value.visitMetaByDate[AppEncounterService.getSessionDate()][encounterName]
        } catch (e) {
            return {}
        }
    }

    async function nextTask() {
        const id = unref(visitID)
        const task = await Offline().nextPatientWorkflowActivity(id)
        if (task && task.patientUrl) {
            return router.push(`${task.patientUrl}/${id}`) 
        }
        return gotoPatientDashboard()
    }

    function resolveOfflineMeta(formComputedData: Record<string, any>) {
        return Object.keys(formComputedData).reduce((a: any, k: any) => {
            if (isPlainObject(formComputedData[k]) && formComputedData[k].offlineMeta) {
                Object.keys(formComputedData[k].offlineMeta).forEach((c) => {
                    a[c] = formComputedData[k].offlineMeta[c]
                })
            }
            return a
        }, {})
    }

    async function saveEncounter(params={} as any) {
        const endTime = new Date().getTime();
        const elapsedTime = (endTime - startTime.value) / 1000; // Time in seconds
        const minutes = elapsedTime / 60;
        try {
            const userAgentObs = await AppEncounterService.buildValueText(
                'Device', getPlatformName()
            )
            const durationObs = await AppEncounterService.buildValueNumber(
                'Review Time', minutes
            )
            const obs = [...(await resolveObs(params.computedData)), userAgentObs, durationObs]
            Offline().saveObsOffline({
                observations: obs,
                id: visitID.value,
                uploadStatus: 'Pending',
                encounterName: params.encounterName,
                encounterTypeID: params.encounterTypeID,
                meta: resolveOfflineMeta(params.computedData),
                labOrders: params.labOrders,
                created: false
            }, false)
            if (!params.skipNextTask) nextTask()
            Offline().synchronizeRecord(visitID.value)
        } catch (e) {
            console.error(`${e}`)
            return toastWarning("Unable to resolve observations")
        }
    }

    function mapStrToOptions(items: Array<string | Option>) {
        return items.map(i => typeof i === 'string' || typeof i === 'number'  
            ?  { label: i, value: i } : i 
        )
    }

    function toOption(label: string, other={}) {
        return { label, value: label, other }
    }

    function gotoPatientDashboard() {
        return router.push(patientDashboardUrl()) 
    }

    function mapOptions(options: Array<string>) {
        return options.map((option) => {
            return { label: option, value: option }
        });        
    }

    function toConditionalOptions(options: Array<Array<string | boolean | undefined>>) {
        const final: Option[] = []
        for(const option of options) {
            if (typeof option[1] === 'boolean') {
                if (option[1]) final.push(toOption(option[0] as string))
                continue
            }
            final.push(toOption(option[0] as string))
        }
        return final
    }

    function resolveObs(obs: any, tag='') {
        const values: any = Object.values(obs)
            .filter((d: any) => d && (d.tag === tag || tag === ''))
            .reduce((accum: any, cur: any) => { 
                const data = cur.obs ? cur.obs : cur
                if (Array.isArray(data)) {
                    accum = accum.concat(data)
                } else if (typeof data === 'function') {
                    accum.push(data())
                } else {
                    accum.push(data)
                }
                return accum
                }, [])
        return Promise.all(values)
    }

    function toYesNoOption(label: string, other: any={}): Option {
        return {
            label,
            value: '',
            other: {
                ...other,
                values: yesNoOptions()
            }
        } 
    }

    function yesNoOptions(): Option[] {
        return [
            { label: "Yes", value: "Yes" },
            { label: "No", value: "No" }
        ]
    }

    function patientDashboardUrl(): string {
        const data = Offline().findByID(visitID.value)
        if (data && data.personID) {
            visitID.value = data.personID
        }
        return `/patient/dashboard/${visitID.value}`
    }

    async function init() {
        visitID.value = (params.visitID || `${route.params.patient_id}`) as any
        const data = Offline().findByID(visitID.value)
        if (data) {
            const person = data.personParams.person
            htsClient.birthEstimated =  data?.personParams?.person.birthdate_estimated || false
            htsClient.birthDate = person.birthdate
            htsClient.age = dayjs(ProgramService.getSessionDate())
                .diff(htsClient.birthDate, 'years')
            htsClient.givenName = person.given_name
            htsClient.familyName = person.family_name
            htsClient.village = person.current_village
            htsClient.currentTA = person.current_traditional_authority
            htsClient.gender = person.gender
            htsClient.district = person.current_district
            htsClient.npid = data.personParams.npid
            if (typeof params.onInitVisit === 'function') params.onInitVisit(htsClient)
            startTime.value = new Date().getTime()
        }
    }

    init()

    return {
        htsClient,
        providerID,
        visitID,
        visitData,
        hasEncounter,
        getEncounter,
        toOption,
        nextTask,
        mapOptions,
        yesNoOptions,
        saveEncounter,
        toYesNoOption,
        resolveObs,
        mapStrToOptions,
        toConditionalOptions,
        gotoPatientDashboard,
        patientDashboardUrl
    }
}
